.btn-bd-primary {
 font-weight:600;
 color:#7952b3;
 border-color:#7952b3
}
.btn-bd-primary:hover,
.btn-bd-primary:active {
 color:#fff;
 background-color:#7952b3;
 border-color:#7952b3
}
.btn-bd-primary:focus {
 box-shadow:0 0 0 3px rgba(121,82,179,0.25)
}
.btn-bd-download {
 font-weight:600;
 color:#ffe484;
 border-color:#ffe484
}
.btn-bd-download:hover,
.btn-bd-download:active {
 color:#2a2730;
 background-color:#ffe484;
 border-color:#ffe484
}
